<template>
    <div class="mu-container" v-if="show">
        <LightGallery
                :images="previewImages"
                :index="previewIndex"
                :disable-scroll="true"
                @close="previewIndex = null"
        />
        <Row :gutter="16">
            <Col :xs="24" :sm="24" :md="24" :lg="{ span: 6, offset: 1 }" :xl="{ span: 4, offset: 5 }"
                 :xxl="{ span: 4, offset: 5 }">
                <Affix :offset-top="10" v-if="!isMobile">
                    <div class="mu-user">
                        <m-user :uuid="userId"></m-user>
                    </div>
                    <div class="mu-photo">
                        <m-photo :uuid="userId"></m-photo>
                    </div>
                </Affix>
            </Col>
            <Col :xs="24" :sm="24" :md="24" :lg="{ span: 16}" :xl="{ span: 10}" :xxl="{ span: 10 }">
                <div class="mu-user" v-if="isMobile" style="margin-bottom: 10px;">
                    <m-user :uuid="userId"></m-user>
                </div>
                <div class="mu-tab" v-if="show">
                    <m-blank v-if="!momentData"></m-blank>
                    <m-item :moment-date="momentData.format_date.date" :moment-week="momentData.format_date.week"
                            :moment-time="momentData.created_at" :moment-desc="momentData.content">
                        <div v-if="momentData.type === 'image'" id="lightgallery">
                            <div v-if="momentData.image.length === 1">
                                <img :src="momentData.image[0].url+'?imageView2/1/w/1600/h/1000'" alt=""
                                     class="mu-card__image" @click="previewImage(0)">
                            </div>
                            <div v-else-if="momentData.image.length % 2 === 0">
                                <div v-for="(imageItem, imageIndex) in momentData.image.length / 2">
                                    <Row :gutter="4">
                                        <Col span="12">
                                            <img :src="momentData.image[imageIndex * 2].url+'?imageView2/1/w/800/h/500'"
                                                 alt="" class="mu-card__image" @click="previewImage(imageIndex * 2)">
                                        </Col>
                                        <Col span="12">
                                            <img :src="momentData.image[imageIndex * 2 + 1].url+'?imageView2/1/w/800/h/500'"
                                                 alt="" class="mu-card__image"
                                                 @click="previewImage(imageIndex * 2 + 1)">
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                            <div v-else-if="momentData.image.length % 3 === 0">
                                <div v-for="(imageItem, imageIndex) in momentData.image.length / 3">
                                    <Row :gutter="4">
                                        <Col span="8">
                                            <img :src="momentData.image[imageIndex * 3].url+'?imageView2/1/w/800/h/800'"
                                                 alt="" class="mu-card__image" @click="previewImage(imageIndex * 3)">
                                        </Col>
                                        <Col span="8">
                                            <img :src="momentData.image[imageIndex * 3 + 1].url+'?imageView2/1/w/800/h/800'"
                                                 alt="" class="mu-card__image"
                                                 @click="previewImage(imageIndex * 3 + 1)">
                                        </Col>
                                        <Col span="8">
                                            <img :src="momentData.image[imageIndex * 3 + 2].url+'?imageView2/1/w/800/h/800'"
                                                 alt="" class="mu-card__image"
                                                 @click="previewImage(imageIndex * 3 + 2)">
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </div>
                        <div v-else-if="momentData.type === 'video'" class="mu-card__video">
                            <m-video :source="momentData.video.url"></m-video>
                        </div>
                    </m-item>
                    <div class="mu-profile">
                        <Icon type="md-arrow-forward" color="#999"/>
                        <a :href="'/?uuid=' + userId">浏览更多关于该用户的时刻</a>
                    </div>
                </div>
            </Col>
        </Row>
        <m-footer></m-footer>
    </div>

</template>

<script>
    import {getMomentDetail} from '@/api/moment';
    import MomentUser from '@/components/MomentUser';
    import MomentItem from '@/components/MomentItem';
    import MomentVideo from '@/components/MomentVideo';
    import MomentPhoto from '@/components/MomentPhoto';
    import MomentFooter from '@/components/MomentFooter';
    import MomentBlank from '@/components/MomentBlank';
    import {LightGallery} from 'vue-light-gallery';

    export default {
        name: "Moment",
        data: function () {
            return {
                uuid: '',
                userId: '',
                momentData: {},
                show: false,
                previewImages: [],
                previewIndex: null,
                isMobile: this.$isMobile
            };
        },
        components: {
            'm-user': MomentUser,
            'm-item': MomentItem,
            'm-video': MomentVideo,
            'm-photo': MomentPhoto,
            'm-footer': MomentFooter,
            'm-blank': MomentBlank,
            LightGallery,
        },
        mounted() {

        },
        created() {
            this.$Spin.show();
            if (typeof this.$route.query.uuid == 'undefined' || this.$route.query.uuid === '') {
                window.location.href = '#/welcome';
            } else {
                this.uuid = this.$route.query.uuid;
                this.requestMoment();
            }
        },
        methods: {
            requestMoment: function () {
                getMomentDetail({
                    'uuid': this.uuid
                }).then((res) => {
                    if (res.data.status === 200) {
                        this.momentData = res.data.data.moment;
                        this.userId = res.data.data.user.uuid;
                    } else {
                        this.$Message.error('opps! 这条时刻丢失了');
                        setTimeout(() => {
                            window.location.href = '/welcome';
                        }, 5000);
                    }
                    this.show = true;
                    this.$Spin.hide();
                })
            },
            previewImage(index) {
                this.previewImages = this.momentData.image;
                this.previewIndex = index;
            }
        }
    }
</script>

<style scoped>
    .mu-profile {
        margin: 30px auto;
        text-align: center;
    }

    .mu-profile > a {
        color: #999;
    }
</style>
